@charset "UTF-8";
@import-normalize;
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body,
blockquote, figure,
form,
h1, h2, h3, h4, h5, h6,
ol, ul, p, pre {
  margin: 0;
}

body {
  font-size: 1.6rem;
}

img {
  max-width: 100%;
}

ul {
  padding-left: 0;
}

li {
  list-style: none;
}

button {
  border: 0;
  background: unset;
  color: currentColor;
  cursor: pointer;
}

:root {
  --color__main: #0073aa;
  --color__accent: #004b6e;
  --color__accent--bg: rgba(0, 115, 170, 0.3);
  --color__txt: #000;
  --color__txt--light: #fff;
  --color__txt--lightBlue: rgb(221, 237, 248);
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 6rem;
}
@media (min-width: 768px) {
  html {
    scroll-padding-top: 0;
  }
}

body {
  font-family: "Open Sans", sans-serif;
}

footer, nav, nav > ul, figcaption {
  background-color: var(--color__main);
  color: var(--color__txt--light);
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
}

h2, h3 {
  color: var(--color__main);
}

h1 {
  font-size: 1.5em;
}

h2 {
  font-size: 2em;
}

main {
  padding-top: 6rem;
}
@media (min-width: 768px) {
  main {
    padding-top: 0;
    margin-left: calc(20rem + 2%);
  }
}

figcaption {
  position: relative;
  top: -2em;
  padding: 0.5em;
  text-align: center;
}
figcaption span {
  margin-left: 1em;
  font-weight: bold;
}
figcaption span:first-of-type {
  margin: 0;
}

img {
  max-width: 100%;
  border: 1px solid var(--color__main);
}

ul {
  padding-left: 0;
  list-style: none;
}

p {
  padding: 1em 0;
}

@media (min-width: 768px) {
  section {
    padding-top: 1em;
  }
}

table {
  width: 100%;
  border-collapse: collapse;
}

thead {
  background: var(--color__accent--bg);
  text-align: left;
}

table, td, th {
  border: 1px solid var(--color__main);
}

td, th {
  padding: 0.5em 1em;
}

.wrapper {
  padding: 4em 2%;
  padding: 0;
  padding-right: 2%;
}

.info-box {
  position: relative;
  padding: 2em 0.5em;
  padding-left: 1em;
  background: var(--color__accent--bg);
  border-left: 3px solid var(--color__accent);
  text-align: center;
}
.info-box:before {
  content: "Tip:";
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.2em 0.5em;
  background: var(--color__accent);
  color: var(--color__txt--light);
}

.cmd {
  color: var(--color__accent);
}

.block-code {
  padding: 1em;
  display: block;
  white-space: pre-wrap;
  background: var(--color__accent--bg);
  border: 1px solid var(--color__main);
}

.inline-code {
  color: var(--color__accent);
  font-style: italic;
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0.5em 2%;
  display: flex;
  flex-wrap: wrap;
  place-content: center space-between;
}
nav > button {
  display: none;
  background: none;
  border: 1px solid var(--color__accent);
  border-radius: 0.5rem;
  box-shadow: 0px 0px 2px 1px var(--color__accent) inset;
  color: var(--color__txt--light);
  cursor: pointer;
  font-size: 1.5em;
}
@media (min-width: 768px) {
  nav > button {
    display: none;
  }
}
nav > button:after {
  content: "â˜°";
}
nav > button span {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  overflow: hidden;
}
nav > button:focus, nav > button:hover {
  background-color: var(--color__accent);
  box-shadow: 0px 0px 2px 1px var(--color__main) inset;
  color: var(--color__txt--lightBlue);
}
nav > ul {
  position: absolute;
  top: 100%;
  right: 0;
  text-align: right;
}
@media (min-width: 768px) {
  nav > ul {
    position: relative;
  }
}
nav > ul li:hover, nav > ul li:focus {
  background-color: var(--color__accent);
}
nav > ul a {
  display: block;
  padding: 0.3em 0.6em;
  color: var(--color__txt--light);
  font-size: 1em;
  text-decoration: none;
}
nav > ul a:hover, nav > ul a:focus {
  color: var(--color__txt--lightBlue);
}
@media (min-width: 768px) {
  nav {
    position: -webkit-sticky;
    position: sticky;
    padding: 0.5em 0;
    float: left;
    display: block;
    height: 100%;
    min-height: 100vh;
    width: 20rem;
    overflow: hidden;
  }
  nav header {
    margin: 0.5em;
    text-align: center;
  }
}

.js nav > ul {
  display: none;
}
@media (min-width: 768px) {
  .js nav > ul {
    display: block;
  }
}
.js nav > ul.is-active {
  display: block;
}
.js nav > button {
  display: block;
}
@media (min-width: 768px) {
  .js nav > button {
    display: none;
  }
}

footer {
  display: flex;
  flex-wrap: wrap;
  gap: 0 2em;
  justify-content: center;
  width: 100%;
  padding: 0.5em 2%;
  text-align: center;
  padding-left: calc(20rem + 2%);
}
@media (min-width: 920px) {
  footer {
    justify-content: space-between;
  }
}
footer a {
  color: var(--color__txt--light);
}
footer a:hover, footer a:focus {
  color: var(--color__txt--lightBlue);
}